d Py Api Configuration') }}</h2>\n\t\t\t\t<p>{{ t('cloud_py_api', 'Configuration allowed only for administrator') }}</p>\n\t\t\t</div>\n\t\t</AppContent>\n\t</Content>\n</template>\n\n<script>\nimport Nextcludl10n from './mixins/Nextcludl10n'\n\nimport Content from '@nextcloud/vue/dist/Components/Content'\nimport AppContent from '@nextcloud/vue/dist/Components/AppContent'\nimport AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'\nimport AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem'\nimport { getCurrentUser } from '@nextcloud/auth'\nimport { generateUrl } from '@nextcloud/router'\n\nexport default {\n\tname: 'CloudPyAPI',\n\tcomponents: {\n\t\tContent,\n\t\tAppContent,\n\t\tAppNavigation,\n\t\tAppNavigationItem,\n\t},\n\tmixins: {\n\t\tNextcludl10n,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tloading: true,\n\t\t\tisAdmin: getCurrentUser() === null ? false : getCurrentUser().isAdmin,\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tif (!this.isAdmin) {\n\t\t\tthis.loading = false\n\t\t}\n\t},\n\tmethods: {\n\t\tgoToAdminSettingsUrl() {\n\t\t\twindow.location.href = generateUrl('/settings/admin/cloud_py_api')\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\nh2 {\n\tmargin: 20px 0;\n}\n</style>\n"],"sourceRoot":""}]);
